import React, { useCallback, useState } from "react";
import { useAuth } from "../contexts/AuthContext";
import { Dimensions, ImageBackground, StyleSheet } from "react-native";
import { Button, Card, Checkbox, Colors, Icon, Image, Text, TextField, Toast, TouchableOpacity, View } from "react-native-ui-lib";
import { image_background, image_eye, image_faceId, image_logo, image_sublogo } from "../../image";
import { colors } from "../../assets/color/color";
import { FontFamily } from "../global";
import { useFocusEffect } from "@react-navigation/native";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { storeUsernameAndPass } from "../contanst";

const Login = ({ navigation }: any) => {
  const { signIn } = useAuth();
  const [email, onChangeEmail] = useState('');
  const [password, onChangePass] = useState('');
  const [secureTextEntry, setSecureTextEntry] = useState(true);
  const [savePassword, setSavePassword] = useState(false);

  useFocusEffect(useCallback(() => {
    (async () => {
      const dataStoreAsync = await AsyncStorage.getItem(storeUsernameAndPass);
      if (dataStoreAsync) {
        const data = JSON.parse(dataStoreAsync)
        onChangeEmail(data.username);
        onChangePass(data.password);
        setSavePassword(data.rememberMe);
      }
    })()
  }, []))

  const handleSubmit = () => {
    const dataBody = {
      "username": email,
      "password": password,
      "rememberMe": savePassword
    }
    signIn(dataBody)
  }
  return (
    <ImageBackground
      resizeMode="cover"
      source={image_background}
      style={styles.imageBackground}
    >
      <View style={{ flex: 1 }}>
        <Image style={styles.logo} source={image_logo} />
      </View>
      <View style={{ flex: 0.2 }}>
        <Text style={styles.headerText} center>Đăng nhập</Text>
      </View>
      {/* <Toast
        visible={true}
        position={'top'}
        autoDismiss={5000}
        message="Đăng nhập thành công"
        
      ></Toast> */}
      <View style={{ flex: 1.2, alignItems: "center", justifyContent: "flex-start" }}>
        <TextField
          containerStyle={{ ...styles.containerTextField, marginBottom: 24 }}
          placeholder={'Tên đăng nhập'}
          floatingPlaceholder
          onChangeText={onChangeEmail}
          enableErrors
          value={email}
          validate={['required', 'email', (value: any) => value.length > 6]}
          validationMessage={['Field is required', 'Email is invalid', 'Password is too short']}
          maxLength={30}
        />

        <View style={{ width: "100%", alignItems: "center", justifyContent: "center", position: "relative" }}>
          <TextField
            containerStyle={styles.containerTextField}
            secureTextEntry={secureTextEntry}
            placeholder={'Mật khẩu'}
            floatingPlaceholder
            onChangeText={onChangePass}
            enableErrors
            value={password}
            validate={['required', 'email', (value: any) => value.length > 6]}
            validationMessage={['Field is required', 'Email is invalid', 'Password is too short']}
            maxLength={30}
          />
          <TouchableOpacity onPress={() => setSecureTextEntry(!secureTextEntry)} style={{ position: "absolute", right: "12%", top: "25%" }}>
            <Icon style={{ width: 20, height: 20, }} source={image_eye} />
          </TouchableOpacity>
        </View>

        <View style={{ flexDirection: "row", justifyContent: "space-between", alignItems: "center", columnGap: width * 0.3 }}>
          <TouchableOpacity onPress={() => setSavePassword(!savePassword)} style={{ flexDirection: "row", alignItems: "center", justifyContent: "center", columnGap: 8 }}>
            <Checkbox containerStyle={{ borderColor: 'rgba(0, 53, 128, 0.35)' }} borderRadius={8} size={16} value={savePassword} onValueChange={() => setSavePassword(!savePassword)} />
            <Text style={{ color: savePassword ? 'rgba(0, 53, 128, 1)' : 'rgba(0, 53, 128, 0.35)', fontWeight: "400", fontSize: 12, fontFamily: FontFamily.interMedium }}>Nhớ tài khoản</Text>
          </TouchableOpacity>
          <TouchableOpacity onPress={() => navigation.navigate('ForgetPassword')}>
            <Text style={{ color: colors.primary, fontWeight: "400", fontSize: 12, fontFamily: FontFamily.interMedium }}>Quên mật khẩu?</Text>
          </TouchableOpacity>
        </View>
        <View style={{ width: "85%", alignItems: "center", justifyContent: "space-between", flexDirection: "row" }}>
          <Button onPress={handleSubmit} labelStyle={{ fontWeight: "500", fontSize: 14, color: "#fff", fontFamily: FontFamily.interMedium }} style={{ height: 40, width: "85%", borderRadius: 8, marginTop: 24 }} label={'Đăng nhập'} size={Button.sizes.large} backgroundColor={colors.primary} />
          <TouchableOpacity>
            <Image style={{ width: 40, height: 40, marginTop: 24 }} source={image_faceId} />
          </TouchableOpacity>
        </View>
      </View>
      <View style={{ flex: 0.4, alignItems: "center", justifyContent: "center" }}>
        <Image style={{ width: 83, height: 52.52 }} source={image_sublogo} />
      </View>
    </ImageBackground>
  )
}

const { width, height } = Dimensions.get("screen")

const styles = StyleSheet.create({
  imageBackground: {
    flex: 1
  },
  containerTextField: {
    borderWidth: 1,
    borderStyle: "solid",
    borderRadius: 8,
    width: "85%",
    paddingHorizontal: 16,
    borderColor: 'rgba(0, 53, 128, 0.15)',
    marginBottom: 16
  },
  logo: {
    width: 278,
    height: 52,
    top: height * 0.18,
    left: width * 0.1
  },
  headerText: {
    color: colors.primary,
    fontWeight: "600",
    fontSize: 24,
    fontFamily: FontFamily.interMedium
  }
})

export default Login;

